"use client";

import { useState, useMemo } from "react";
import DateFiltersExports from "@/components/DateFiltersExports";

export default function DateFiltersWrapper({ inventory }) {
    const [startDate, setStartDate] = useState(null);
    const [endDate, setEndDate] = useState(null);
    const [selectedLocations, setSelectedLocations] = useState([]);
    const [selectedProducts, setSelectedProducts] = useState([]);

    const locationOptions = [...new Set(inventory.map(i => i.location.name))].map(name => ({ value: name, label: name }));
    const productOptions = inventory.map(i => ({ value: i.product.id, label: i.product.name }));

    const filteredInventory = useMemo(() => {
        return inventory.filter((item) => {
            const inDateRange =
                (!startDate || new Date(item.createdAt) >= startDate) &&
                (!endDate || new Date(item.createdAt) <= endDate);
            const inLocations = selectedLocations.length === 0 || selectedLocations.includes(item.location.name);
            const inProducts = selectedProducts.length === 0 || selectedProducts.includes(item.product.id);
            return inDateRange && inLocations && inProducts;
        });
    }, [inventory, startDate, endDate, selectedLocations, selectedProducts]);

    const exportCSV = () => { /* CSV export logic */ };
    const exportPDF = () => { /* PDF export logic */ };

    return (
        <DateFiltersExports
            view="stock-report"
            startDate={startDate}
            endDate={endDate}
            setStartDate={setStartDate}
            setEndDate={setEndDate}
            selectedLocations={selectedLocations}
            setSelectedLocations={setSelectedLocations}
            selectedProducts={selectedProducts}
            setSelectedProducts={setSelectedProducts}
            locationOptions={locationOptions}
            categoryOptions={[]} // optional
            productOptions={productOptions}
            exportCSV={exportCSV}
            exportPDF={exportPDF}
            setView={() => { }}
        />
    );
}
